[class*=rcfm-component-ico-]
  display: inline-block
  vertical-align: baseline
  position: relative
  width: 1em
  height: 1em
  font-size: $component-icon-size
  line-height: 1
  overflow: hidden
  -webkit-user-select: none
  -moz-user-select: none
  -ms-user-select: none
  user-select: none
  object-fit: contain
  object-position: center
  font-style: normal
  background-position: center
  background-size: contain

[class*=rcfm-component-ico-]::before,
[class*=rcfm-component-ico-]::after
  display: block
  position: absolute
  top: 50%
  left: 50%
  transform: translate(-50%, -50%)



.rcfm-component-ico-left::before,
.rcfm-component-ico-right::before,
.rcfm-component-ico-up::before,
.rcfm-component-ico-down::before
  width: 0.6em
  height: 0.6em
  border: $component-icon-stroke-width solid
  border-right: none
  border-bottom: none
  content: ''

.rcfm-component-ico-left::before
  transform: translate(-25%, -50%) rotate(-45deg)

.rcfm-component-ico-right::before
  transform: translate(-75%, -50%) rotate(135deg)

.rcfm-component-ico-up::before
  transform: translate(-50%, -25%) rotate(45deg)

.rcfm-component-ico-down::before
  transform: translate(-50%, -75%) rotate(225deg)



.rcfm-component-ico-plus::before,
.rcfm-component-ico-plus::after,
.rcfm-component-ico-cross::before,
.rcfm-component-ico-cross::after
  content: ''
  background: currentColor

.rcfm-component-ico-plus::before,
.rcfm-component-ico-cross::before
  width: 0.9em
  height: $component-icon-stroke-width

.rcfm-component-ico-plus::after,
.rcfm-component-ico-cross::after
  width: $component-icon-stroke-width
  height: 0.9em

.rcfm-component-ico-cross::before,
.rcfm-component-ico-cross::after
  transform: translate(-50%, -50%) rotate(45deg)



.rcfm-component-ico-menu::before
  width: 0.9em
  height: $component-icon-stroke-width
  content: ''
  background: currentColor
  box-shadow: 0 -0.35em, 0 0.35em



.rcfm-component-ico-search::before
  top: 5%
  left: 5%
  width: 0.75em
  height: 0.75em
  border: $component-icon-stroke-width solid
  content: ''
  border-radius: 50%
  transform: translate(0, 0) rotate(45deg)

.rcfm-component-ico-search::after
  top: 80%
  left: 80%
  width: 0.4em
  height: $component-icon-stroke-width
  content: ''
  background: currentColor
  transform: translate(-50%, -50%) rotate(45deg)



.rcfm-component-ico-more::after,
.rcfm-component-ico-more::before
  content: ''
  height: $component-icon-stroke-width
  background: currentColor

.rcfm-component-ico-more::before
  box-shadow: 0 0.7em 0 0
  top: 0.1em
  width: 72%
  transform: translate(-50%, 0)

.rcfm-component-ico-more::after
  width: 100%
  top: 0
  bottom: 0
  left: auto
  margin: auto
  transform: none



.rcfm-component-ico-arrow-up::after,
.rcfm-component-ico-arrow-up::before
  content: ''
  display: block
  box-sizing: border-box
  position: absolute
  top: 0

.rcfm-component-ico-arrow-up::after
  width: 80%
  height: 80%
  border-top: solid $component-icon-stroke-width
  border-left: solid $component-icon-stroke-width
  transform: translate(-50%, 17%) rotate(45deg)

.rcfm-component-ico-arrow-up::before
  width: $component-icon-stroke-width
  height: 100%
  background: currentColor
  transform: translate(-50%, 0)



.rcfm-component-ico-arrow-down::after,
.rcfm-component-ico-arrow-down::before
  content: ''
  display: block
  box-sizing: border-box
  position: absolute
  top: 0

.rcfm-component-ico-arrow-down::after
  width: 80%
  height: 80%
  border-bottom: solid $component-icon-stroke-width
  border-left: solid $component-icon-stroke-width
  transform: translate(-50%, 7%) rotate(-45deg)

.rcfm-component-ico-arrow-down::before
  width: $component-icon-stroke-width
  height: 100%
  background: currentColor
  transform: translate(-50%, 0)
