.rcfm-subform-container
  border: solid $subform-container-border-width $subform-container-border-color
  border-radius: $subform-container-border-radius
  &.rcfm-component-highlight-off
    padding: $subform-container-padding
    background: $subform-container-background
  &:not(.rcfm-component-highlight-off)
    background: $subform-container-background
    border-color: $component-form-element-invalid-border-color
    box-shadow: $component-form-element-invalid-box-shadow

.rcfm-subform-descendants
  display: block
  border: none
  opacity: 1

.rcfm-subform-descendants[disabled]
  & input.rcfm-fmtag,
  & select.rcfm-fmtag,
  & textarea.rcfm-fmtag
    cursor: not-allowed
    opacity: $component-form-tag-disabled-opacity
  & [class*=rcfm-component-btn-]
    cursor: not-allowed
    opacity: $component-button-disabled-opacity

.rcfm-subform-fieldset-descendants > *
  margin: $subform-fieldset-descendants-element-margin

.rcfm-subform-fieldset-actions > *
  margin: $subform-fieldset-actions-element-margin



.rcfm-subform-previewer-container
  border-radius: $subform-previewer-container-border-radius
  border-top-left-radius: 0
  border-top-right-radius: 0

.rcfm-subform-previewer-container[data-disabled]
  & input.rcfm-fmtag,
  & select.rcfm-fmtag,
  & textarea.rcfm-fmtag
    opacity: $component-form-tag-disabled-opacity

.rcfm-form-builder-member.rcfm-form-builder-subform,
.rcfm-component-highlight-error.rcfm-subform-previewer-container
  overflow: visible

.rcfm-subform-previewer-container
  &.rcfm-component-highlight-off
    padding: $subform-previewer-container-padding
    background: $subform-previewer-container-background

.rcfm-subform-fieldset-previewer .rcfm-form-builder-actions
  margin-bottom: 0
  background: $subform-fieldset-member-actions-background
  border: solid $subform-fieldset-member-actions-border-width $subform-fieldset-member-actions-border-color

.rcfm-subform-fieldset-previewer .rcfm-member-previewer
  border: solid $subform-fieldset-member-previewer-border-width $subform-fieldset-member-previewer-border-color
  border-top: none
  & .rcfm-component-highlight-error
    background: $subform-fieldset-member-previewer-highlight-error-on-background

.rcfm-form-builder-inserter.rcfm-form-builder-subform-inserter,
.rcfm-form-builder-actions.rcfm-form-builder-subform-fieldset-actions
  display: none

