.rcfm-member-prop-editor-options-item
  margin: $member-prop-editor-options-item-margin

.rcfm-member-prop-editor-options-item:first-child
  margin-top: 0

.rcfm-member-prop-editor-options-item:last-child
  margin-bottom: 0

.rcfm-member-prop-editor-options-item-row
  display: flex
  justify-content: space-between
  align-items: center

.rcfm-member-prop-editor-options-item-row > [class*=rcfm-]
  flex: 1 1 auto
  margin: $member-prop-editor-options-item-child-margin

.rcfm-member-prop-editor-options-item-row > .rcfm-btngroup-x
  flex: 0 0 auto

.rcfm-member-prop-editor-options-item-row > .rcfm-fmlabel
  flex: 0 0 auto
  margin: $member-prop-editor-options-item-label-margin

.rcfm-member-prop-editor-options-item-row > [class*=rcfm]:first-child
  margin-left: 0

.rcfm-member-prop-editor-options-item-row > [class*=rcfm]:last-child
  margin-right: 0
