.rcfm-form-builder
  background: $form-builder-background

.rcfm-form-builder-previewer
  padding: $form-builder-previewer-padding

.rcfm-form-builder-previewer > [class*=rcfm]
  max-width: $form-builder-previewer-children-max-width
  margin-left: auto
  margin-right: auto

.rcfm-form-builder-previewer > [class*=rcfm]:first-child
  margin-top: 0

.rcfm-form-builder-previewer > [class*=rcfm]:last-child
  margin-bottom: 0

.rcfm-form-builder-inserter-of-root
  padding: $form-builder-inserter-of-root-padding

.rcfm-form-builder-member
  overflow: hidden
  margin: $form-builder-member-margin
  border-radius: $form-builder-member-border-radius
  box-shadow: $form-builder-member-box-shadow

.rcfm-form-builder-actions
  position: relative
  display: flex
  justify-content: space-between
  align-items: center
  padding: $form-builder-actions-padding
  background: $form-builder-actions-background
  border-radius: $form-builder-member-border-radius
  border-bottom-left-radius: 0
  border-bottom-right-radius: 0

.rcfm-form-builder-actions > [class*=rcfm-]
  margin: $form-builder-actions-child-margin

  &:first-child
    margin-left: 0

  &:last-child
    margin-right: 0

  &.rcfm-form-builder-action-delete
    margin-right: auto

.rcfm-form-builder-member .rcfm-member-previewer
  border-radius: $form-builder-member-border-radius
  border-top-left-radius: 0
  border-top-right-radius: 0

.rcfm-form-builder-descendants
  padding: $form-builder-descendants-padding

.rcfm-form-builder-inserter
  position: relative
  display: flex
  justify-content: center
  align-items: center
  margin: $form-builder-inserter-margin

.rcfm-form-builder-inserter-child-select,
.rcfm-form-builder-editor
  position: fixed
  z-index: $z-index-high
  top: 0
  right: 0
  display: flex
  flex-direction: column
  justify-content: flex-end
  height: 100%
  width: 100%
  background: $form-builder-modal-background

.rcfm-form-builder-inserter-child-select-container,
.rcfm-form-builder-editor-container
  max-height: $form-builder-modal-content-max-height
  overflow: auto
  -webkit-overflow-scrolling: touch

.rcfm-form-builder-previewer [class*=rcfm].rcfm-form-builder-exception
  position: fixed
  z-index: $z-index-higher
  max-width: $form-builder-exception-max-width
  top: $form-builder-exception-position-top
  right: $form-builder-exception-position-right
  bottom: $form-builder-exception-position-bottom
  left: $form-builder-exception-position-left
  text-align: center
  box-shadow: $form-builder-exception-box-shadow

.rcfm-form-builder-fieldset
  margin: $form-builder-fieldset-margin

@media (min-width: $width-breakpoint-small)
  .rcfm-form-builder,
  .rcfm-form-builder-editor,
  .rcfm-form-builder-previewer
    height: 100%

  .rcfm-form-builder
    display: flex
    justify-content: center

  .rcfm-form-builder-previewer,
  .rcfm-form-builder-editor
    flex: 1 1 50%
    overflow: auto

  .rcfm-form-builder-previewer
    padding: $form-builder-previewer-padding-medium

  .rcfm-form-builder-inserter-child-select
    position: absolute
    right: auto
    height: auto
    width: auto
    background: none

  .rcfm-form-builder-inserter-child-select-container
    max-height: $form-builder-modal-content-max-height-medium
    box-shadow: $form-builder-insert-child-select-box-shadow-medium

  .rcfm-form-builder-editor
    position: initial
    display: block
    height: auto
    width: auto
    background: $form-builder-editor-background
    box-shadow: $form-builder-editor-box-shadow-medium

  .rcfm-form-builder-editor-container
    max-height: initial
    min-height: 100%
    padding: $form-builder-editor-padding-medium
    overflow: visible
