.rcfm-member-prop-editor-condition-branch-title
  margin: $member-prop-editor-condition-branch-title-margin
  font-size: $member-prop-editor-condition-branch-title-font-size
  font-weight: $member-prop-editor-condition-branch-title-font-weight
  overflow: hidden
  line-height: 1
  white-space: nowrap
  text-overflow: ellipsis

.rcfm-member-prop-editor-condition-when-branch

  & > .rcfm-component-btngroup-x
    margin: $member-prop-editor-condition-when-branch-button-group-margin

  & > .rcfm-component-highlight-error
    margin: $member-prop-editor-condition-rule-margin

    &:first-child
      margin-top: 0

    &:last-child
      margin-bottom: 0

.rcfm-member-prop-editor-condition-rule,
.rcfm-member-prop-editor-condition-rule > .rcfm-component-highlight-error
  margin: $member-prop-editor-condition-rule-row-margin

  &:first-child
    margin-top: 0

  &:last-child
    margin-bottom: 0

.rcfm-member-prop-editor-condition-rule-row
  display: flex
  justify-content: space-between
  align-items: center

  & > [class*=rcfm-]
    flex: 1 1 auto
    margin: $member-prop-editor-condition-rule-row-margin

  & > .rcfm-member-prop-editor-condition-rule-logic
    flex: 0 0 $member-prop-editor-condition-rule-logic-width

  & > .rcfm-component-btngroup-x
    flex: 0 0 auto

  & > [class*=rcfm-]:first-child
    margin-left: 0

  & > [class*=rcfm-]:last-child
    margin-right: 0
