.rcfm-fieldset
  padding: $fieldset-padding
  background: $fieldset-background

.rcfm-fieldset-title
  margin: $fieldset-title-margin
  font-size: $fieldset-title-font-size
  line-height: $fieldset-title-line-height
  font-weight: $fieldset-title-font-weight
  color: $fieldset-title-color

.rcfm-fieldset-description
  margin: $fieldset-description-margin
  font-size: $fieldset-description-font-size
  line-height: $fieldset-description-line-height
  color: $fieldset-description-color
