.rcfm-component-alert,
.rcfm-component-alert-info,
.rcfm-component-alert-success,
.rcfm-component-alert-error,
.rcfm-component-alert-warning
  z-index: $z-index-higher
  display: block
  padding: $component-alert-padding
  overflow: hidden
  border-radius: $component-alert-border-radius
  box-shadow: $component-alert-box-shadow

.rcfm-component-alert-title
  margin: $component-alert-title-margin
  font-size: $component-alert-title-font-size
  line-height: $component-alert-title-line-height
  font-weight: $component-alert-title-font-weight

.rcfm-component-alert-text
  margin: $component-alert-text-margin
  font-size: $component-alert-text-font-size
  line-height: $component-alert-text-line-height

.rcfm-component-alert-info
  color: $component-alert-info-color
  background: $component-alert-info-background

.rcfm-component-alert-success
  color: $component-alert-success-color
  background: $component-alert-success-background

.rcfm-component-alert-error
  color: $component-alert-error-color
  background: $component-alert-error-background

.rcfm-component-alert-warning
  color: $component-alert-warning-color
  background: $component-alert-warning-background
