.rcfm-component-fmlabel
  display: block
  font-size: $component-form-label-font-size
  line-height: $component-form-label-line-height
  color: $component-form-label-color

.rcfm-component-fmlabel-required.rcfm-component-fmlabel::after
  content: $component-form-label-required-content
  color: $component-form-label-required-content-color

.rcfm-component-fmlabel-small.rcfm-component-fmlabel
  font-size: $component-form-label-small-font-size
  line-height: $component-form-label-small-line-height

.rcfm-component-fmlabel-large.rcfm-component-fmlabel
  font-size: $component-form-label-large-font-size
  line-height: $component-form-label-large-line-height
