.rcfm-component-highlight,
.rcfm-component-highlight-info,
.rcfm-component-highlight-success,
.rcfm-component-highlight-error,
.rcfm-component-highlight-warning
  display: block
  padding: $component-highlight-on-padding
  overflow: hidden
  border-radius: $component-highlight-border-radius
  box-shadow: $component-highlight-on-box-shadow

.rcfm-component-highlight-info
  background: $component-highlight-info-on-background

.rcfm-component-highlight-success
  background: $component-highlight-success-on-background

.rcfm-component-highlight-error
  background: $component-highlight-error-on-background

.rcfm-component-highlight-warning
  background: $component-highlight-warning-on-background

[class*=rcfm-component-highlight].rcfm-component-highlight-off
  padding: $component-highlight-off-padding
  background: none
  box-shadow: $component-highlight-off-box-shadow

[class*=rcfm-component-highlight] > .rcfm-component-highlight-hint-bottom
  margin-top: $component-highlight-hint-margin-y

[class*=rcfm-component-highlight] > .rcfm-component-highlight-hint-top
  margin-bottom: $component-highlight-hint-margin-y
